
import "xterm/css/xterm.css";

import {
  computed,
  defineComponent,
  nextTick,
  onMounted,
  reactive,
  ref,
  toRefs,
  watch,
  watchEffect,
} from "vue";
import xhttp, { Xhttp } from "../xhttp";
import { Terminal } from "xterm";
import { AttachAddon } from "xterm-addon-attach";
import { FitAddon } from "xterm-addon-fit";
import { ElMessage, ElPopover } from "element-plus";
import { Router, useRoute, useRouter } from "vue-router";

let route: Router;

enum Mode {
  "create" = 0,
  "update" = 1,
}

interface Host {
  id: number;
  name: string;
  address: string;
  user: string;
  pwd: string;
  port: number;
  font_size: number;
  background: string;
  foreground: string;
  cursor_color: string;
  font_family: string;
  cursor_style: "block" | "underline" | "bar";
  shell: string;
  session_id: string;
  term: Terminal;
  fit: FitAddon;
}

interface VerifyFromData {
  host: Host;
  is_success: boolean;
}

interface Path {
  dir: string;
  name: string;
}

interface File {
  name: string;
  mod_time: string;
  mode: string;
  path: string;
  type: "d" | "f";
  size: number;
}

interface DirInfo {
  current_dir: string;
  dir_count: number;
  file_count: number;
  files: Array<File>;
  paths: Array<Path>;
}

let data = reactive({
  mode: Mode.create,
  id: 0,
  name: "",
  address: "",
  user: "",
  pwd: "",
  port: 22,
  h: 20,
  w: 80,
  session_id: "",
  background: "#000000",
  foreground: "#FFFFFF",
  cursor_color: "#FFFFFF",
  font_family: "Courier",
  font_size: 16,
  cursor_style: "block",
  shell: "bash",

  upload_path: "",
  download_path: "",
  host_list: [] as Array<Host>,
  host_tabs: [] as Array<Host>,

  current_host: { session_id: "" } as Host,
  host_dialog_visible: false,
  file_dialog_visible: false,
  dir_info: {} as DirInfo,
});

const title = computed(() => {
  let titleHtml = `<span style="color:red;">当前名称:${data.current_host.name} &nbsp;&nbsp;&nbsp;当前主机:${data.current_host.address}</span>`;
  return titleHtml;
});

/**
 * 验证输入的主机信息
 */
function verifyFrom(): VerifyFromData {
  let verifyFromData: VerifyFromData = {
    host: {} as Host,
    is_success: false,
  };

  if (data.name.length === 0) {
    ElMessage.error("名称不能为空");
    return verifyFromData;
  }

  if (data.name.length > 30) {
    ElMessage.error("名称不能大于30个字符");
    return verifyFromData;
  }

  if (data.address.length === 0) {
    ElMessage.error("主机不能为空");
    return verifyFromData;
  }

  if (data.address.length > 60) {
    ElMessage.error("主机不能大于60个字符");
    return verifyFromData;
  }

  if (data.user.length === 0) {
    ElMessage.error("用户名不能为空");
    return verifyFromData;
  }

  if (data.user.length > 60) {
    ElMessage.error("用户名不能大于60个字符");
    return verifyFromData;
  }

  if (data.user.length === 0) {
    ElMessage.error("用户名不能为空");
    return verifyFromData;
  }

  if (data.user.length > 60) {
    ElMessage.error("用户名不能大于60个字符");
    return verifyFromData;
  }

  if (data.pwd.length === 0) {
    ElMessage.error("密码不能为空");
    return verifyFromData;
  }

  if (data.user.length > 60) {
    ElMessage.error("密码不能大于60个字符");
    return verifyFromData;
  }

  if (!data.port) {
    ElMessage.error("端口输入错误,必须是1-65535");
    return verifyFromData;
  }

  if (data.port < 1 || data.port > 65535) {
    ElMessage.error("端口范围错误,必须是1-65535");
    return verifyFromData;
  }
  let h = {
    id: data.id,
    name: data.name,
    address: data.address,
    user: data.user,
    pwd: data.pwd,
    port: data.port,
    session_id: data.session_id,
    background: data.background,
    foreground: data.foreground,
    cursor_color: data.cursor_color,
    font_family: data.font_family,
    font_size: data.font_size,
    cursor_style: data.cursor_style,
    shell: data.shell,
  };
  let result: VerifyFromData = {
    host: h as Host,
    is_success: true,
  };
  return result;
}

/**
 * 清空表单数据
 */

function cleanFrom() {
  data.id = 0;
  data.name = "";
  data.address = "";
  data.user = "";
  data.pwd = "";
  data.port = 22;
  data.session_id = "";
  data.background = "#000000";
  data.foreground = "#FFFFFF";
  data.cursor_color = "#FFFFFF";
  data.font_family = "Courier";
  data.font_size = 16;
  data.cursor_style = "block";
  data.shell = "bash";
}

/**
 * 连接
 */
function connect() {
  let result = verifyFrom();
  if (!result.is_success) {
    return;
  }
  connectHost(result.host);
}

